@import url('css/variables.css');

.modal-wrapper {
  position: fixed;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  background: rgba(0, 0, 0, 0.4);

  display: flex;
  justify-content: center;
  align-items: center;

  padding: 10px;

  opacity: 0;
  pointer-events: none;
  transition: 0.3s ease;

  z-index: 100;
}

.modal-wrapper.active {
  opacity: 1;
  pointer-events: all;
}

.modal {
  position: relative;
  width: 100%;
  max-width: 541px;
  padding: 40px;

  background: #fff;

  border-radius: 12px;
}

.close-button {
  position: absolute;
  top: 16px;
  right: 16px;

  padding: 0;

  width: 24px;
  height: 24px;

  background: url(img/svg/close.svg);
  background-repeat: no-repeat;
  background-size: cover;
}

.img-box {
  width: 100%;
  height: 180px;

  background: rgba(0, 0, 0, 0.1);
  border-radius: 14px;
  overflow: hidden;

  margin-bottom: 14px;
}

@media screen and (min-width: 500px) {
  .img-box {
    height: 248px;
  }
}

.img-box > img {
  width: 100%;
  height: 100%;
}

.car-title {
  color: var(--main-text-color);
  font-size: 16px;
  font-weight: 500;
  line-height: 1.33;

  margin-bottom: 6px;
}

@media screen and (min-width: 500px) {
  .car-title {
    font-size: 18px;
  }
}

.car-title > span {
  color: var(--main-sub-color);
}

.car-info-box {
  margin-bottom: 14px;

  color: rgba(18, 20, 23, 0.5);
  font-size: 12px;
  line-height: 1.5;
}

.car-info {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}

.car-description {
  font-size: 12px;
  line-height: 1.42;
}

@media screen and (min-width: 500px) {
  .car-description {
    font-size: 14px;
  }
}

.modal-rent-button {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  max-width: 168px;
}

.modal-sec-title {
  margin-top: 24px;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.42;
}

.modal-acc-func {
  color: rgba(18, 20, 23, 0.5);
  font-size: 12px;
  line-height: 1.5;

  display: flex;
  flex-direction: column;
  gap: 4px;
}

.rental-cond-box {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;

  margin-bottom: 24px;
}

.rental-cond-box > p {
  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 35px;
  background: #f9f9f9;

  padding: 7px 14px;

  color: #363535;
  font-family: Montserrat, sans-serif;
  font-size: 12px;
  line-height: 1.5;
  letter-spacing: -0.24px;
}

.rental-cond-box > p > span {
  font-weight: 600;
  color: var(--main-sub-color);
}
